<template>
    <div class="body">
        <div class="header">
            <van-nav-bar fixed placeholder title="订单管理" left-text="返回" @click-left="goBack" left-arrow>
                <van-icon name="search" slot="right" />
            </van-nav-bar>
            <van-search @search="searchItem" v-model="value" shape="round" background="#4fc08d" placeholder="请输入搜索关键词" />
        </div>
        <div class="main">
            <div v-for="order in orders" :key="order.oid">
                <van-panel :title="order.item.name" :desc="order.item.comment" :status="order.item.price + '¥'">
                <div style="padding-right: 1rem;">
                    <div style="display: flex; justify-content: space-between;">
                        <span>{{ order.address.name }}</span>
                        <span>{{ order.address.tel }}</span>
                    </div>
                    <div style="color: gray; font-size: 0.5rem;">
                        {{ order.address.province + " " + order.address.city + " " + order.address.county }}
                    </div>
                </div>
                <div slot="footer">
                    <van-button @click="detailOrder(order.oid)" style="width: 4.5rem;" size="small" type="primary">查看详细</van-button>
                    <div style="display: inline-block; width: 2rem ;">&nbsp;</div>
                    <van-button @click="delOrder(order.oid)" style="width: 4.5rem;" size="small" type="danger">删除订单</van-button>
                </div>
                </van-panel>
                <br>
            </div>
        </div>
        <div class="footer">
            <div class="footer">
                <van-pagination @change="pageChange" v-model="currentPage" :total-items="count" :items-per-page="5"
                    :show-page-size="4" force-ellipses />
            </div>
        </div>
    </div>
</template>
<style>
.body {
    min-height: 100vh;
    background-color: #f1f5f7;
}

.header {
    z-index: 999;
}

.main {
    margin-top: 3rem;
}


.footer {
    position: fixed;
    bottom: 0%;
    width: 100%;
    z-index: 999;
}

.van-panel__content {
        height: 3rem;
        text-align: left;
        padding-left: 1rem;
        padding-top: 0.5rem;
    }
.van-cell__title, .van-cell__value{
    text-align: left;
}
.van-panel__header-value{
    text-align: right;
}
.van-panel__footer{
    text-align: right;
}

</style>
<script>
import axios from 'axios';
import { Toast } from 'vant';

const options = {
    data() {
        return {
            value: "",
            currentPage: 1,
            count: 100,
            orders:[],
        }
    },
    methods: {
        goBack() {
            this.$router.push("/person")
        },
        searchItem() {

        },
        getPageOrderDetails() {
            axios.get(`/orders/1/5`).then((resp) => {
                if (!resp.data.data) {
                    Toast(resp.data.msg);
                }
                this.orders = resp.data.data;
            });
        },
        pageChange(){
            console.log(this.currentPage);
            axios.get(`/orders/${this.currentPage}/${5}`).then((resp) => {
                this.orders = resp.data.data;
                // Toast(resp.data.msg);
            });
        },
        ordersCount(){
            axios.get("/orders/count").then((resp) => {
                this.count = parseInt(resp.data.data);
            });
        },
        detailOrder(oid){
            console.log(oid);
            Toast("功能开发中...")
        },
        delOrder(oid){
            console.log(oid)
            Toast("功能开发中...")
        }
    },
    mounted() {
        if (!localStorage.getItem("user")) {
            Toast("非法操作!");
        }
        this.ordersCount();
        this.getPageOrderDetails();
    }
};

export default options;
</script>